import React from 'react';
import * as modals from './modals';
import { useModalContext } from '../providers/ModalContext';
import { usePatternsWizardOverridesContext } from '../providers';

export const ModalRenderer = () => {
  const { modal, closeModal } = useModalContext();
  const overrides = usePatternsWizardOverridesContext();

  if (!modal.id) {
    return null;
  }

  const ModalComponent =
    overrides?.modals?.[modal.id] ||
    (modals as Record<string, React.ComponentType<any>>)[modal.id];

  if (!ModalComponent) {
    console.warn(`No modal registered for id: ${modal.id}`);
    return null;
  }

  return <ModalComponent {...modal.props} onClose={closeModal} />;
};
